/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

a {
  color: black; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

a {
  text-decoration: none; }

body,
html {
  overflow: hidden; }

.top-banner {
  position: relative;
  height: 30px;
  background-color: #fff7e4;
  z-index: 10; }

.top-banner__list {
  list-style: none;
  float: right; }

.top-banner__item {
  font-family: 'Cabin', sans-serif;
  font-weight: 900;
  display: inline-block;
  padding: 7px 14px;
  color: black; }

.top-banner__icons {
  display: none;
  position: absolute;
  float: left; }

.nav {
  position: relative;
  height: 60px;
  background-color: rgba(255, 247, 228, 0.85);
  z-index: 9; }

.nav__logo {
  float: left;
  padding: 2px 25px 0px 18px;
  line-height: 58px;
  font-family: 'Tangerine', cursive;
  font-weight: 700;
  font-size: 36px;
  letter-spacing: 1px;
  box-sizing: border-box;
  color: #fff7e4;
  background-color: #352800; }

.nav__icons {
  float: right;
  font-size: 30px;
  padding: 14px 25px 14px 25px;
  color: #352800; }
  .nav__icons:hover {
    animation: 0.2s ease-in-out forwards fadeInTabHighlight; }

.nav__list {
  position: relative;
  height: 100%; }

.nav__list-item:hover .nav__link {
  animation: 0.2s ease-in-out forwards fadeInTabHighlight; }

.nav__list-item:hover .submenu {
  z-index: 4;
  display: block;
  opacity: 1;
  animation: 0.2s ease-in-out fadeInSubmenu; }

.nav__link {
  position: relative;
  float: left;
  text-transform: capitalize;
  font-family: 'Lora', serif;
  font-size: 20px;
  padding: 20px 25px 20px 25px;
  font-weight: 700;
  color: #352800; }

.submenu {
  opacity: 0;
  text-align: center;
  top: 60px;
  position: absolute;
  display: none;
  background-color: #fff7e4;
  box-sizing: border-box;
  width: 100vw;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.666); }

.submenu__container {
  display: inline-block;
  position: relative; }

.promo-section {
  float: left;
  text-align: center;
  padding: 0 50px 0 50px; }

.promo-section__content {
  background-image: url("../images/coffee-promo-1.png");
  width: calc(380px/2);
  height: calc(504px/2);
  background-size: contain; }

.section {
  text-align: left;
  float: left;
  padding: 45px 60px 40px 60px; }

.section__title {
  text-align: left;
  display: inline-block;
  padding-bottom: 25px;
  font-weight: bold;
  font-size: 24px;
  font-family: 'Lora', serif;
  font-weight: 800; }

.section__link {
  position: relative;
  text-align: left;
  width: 100%;
  color: black;
  opacity: 0.66;
  display: block;
  padding: 10px 0px;
  font-family: 'Cabin', sans-serif;
  font-size: 17px; }
  .section__link:hover {
    opacity: 1;
    color: black;
    transition: 0.2s opacity ease-in-out;
    transition: 0.2s color ease-in-out; }

@keyframes fadeInSubmenu {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes fadeInTabHighlight {
  from {
    background-color: unset; }
  to {
    background-color: #fff7e4;
    color: black; } }

@media (max-width: 576px) {
  .nav {
    background-color: green; }
  .nav__list-item {
    display: block; } }

